const API_BASE_URL = process.env.REACT_APP_API_URL || "http://127.0.0.1:5000";

// Register a new user
export const registerUser = async (userData) => {
    try {
        const response = await fetch(`${API_BASE_URL}/auth/register`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(userData),
        });

        const data = await response.json();

        if (!response.ok) {
            throw new Error(data.error || "Registration failed");
        }

        return data;
    } catch (error) {
        console.error("Registration error:", error);
        return { error: error.message };
    }
};

// Login user
export const loginUser = async (credentials) => {
    try {
        const response = await fetch(`${API_BASE_URL}/auth/login`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(credentials),
        });

        const data = await response.json();

        if (!response.ok) {
            throw new Error(data.error || "Login failed");
        }

        return data;
    } catch (error) {
        console.error("Login error:", error);
        return { error: error.message };
    }
};

// Get user profile data
export const getUserProfile = async (userId) => {
    try {
        const response = await fetch(`${API_BASE_URL}/user/profile/${userId}`);
        const data = await response.json();

        if (!response.ok) {
            throw new Error(data.error || "Failed to fetch profile");
        }

        return data;
    } catch (error) {
        console.error("Error fetching user profile:", error.message);
        throw error;
    }
};

// Update user profile (including username, email, avatar, and language)
export const updateUserProfile = async (userId, updatedData) => {
    try {
        const response = await fetch(`${API_BASE_URL}/user/profile/${userId}`, {
            method: "PUT",
            headers: {
                "Content-Type": "application/json",
                "Authorization": `Bearer ${localStorage.getItem("access_token")}` // Add the JWT token for authorization
            },
            body: JSON.stringify(updatedData),  // The data to be updated
        });

        const data = await response.json();

        if (!response.ok) {
            throw new Error(data.error || "Failed to update profile");
        }

        return data;
    } catch (error) {
        console.error("Error updating profile:", error);
        return { error: error.message };
    }
};

// Send image data for emotion detection
export const sendDetectionData = async (imageData) => {
    const response = await fetch(`${API_BASE_URL}/detect-emotion`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ image: imageData }),
    });

    return response.json();
};
